import _ from 'lodash';

const API_KEY = "4d023467"; /*type in your api key manually*/

export const requestQuotes = _.memoize(async title => {
    const res = await fetch(`https://www.omdbapi.com/?t=${title}&apikey=${API_KEY}`)
    if (res.status !== 200) return [];

    const quotesArray = await res.json();
    return quotesArray;
});